import React, { useState, useEffect } from 'react';
import { Email, Phone, Cake, LocationOn, Work, School, Language, Info, People, Star } from "@mui/icons-material";
import { Box, Typography, TextField, Button, Container, MenuItem, Select as MuiSelect } from '@mui/material'; // Renamed Select to MuiSelect
import jsPDF from 'jspdf';
import 'jspdf-autotable';
import html2canvas from 'html2canvas';
import Select from 'react-select'; // Kept Select from react-select

const languageOptions = [
  { value: 'arabic', label: 'العربية' },
  { value: 'english', label: 'الإنجليزية' },
  { value: 'french', label: 'الفرنسية' },
  { value: 'spanish', label: 'الإسبانية' },
  { value: 'german', label: 'الألمانية' },
  { value: 'chinese', label: 'الصينية' },
  { value: 'japanese', label: 'اليابانية' },
  { value: 'russian', label: 'الروسية' },
  { value: 'italian', label: 'الإيطالية' },
  { value: 'portuguese', label: 'البرتغالية' },
  { value: 'hindi', label: 'الهندية' },
  { value: 'bengali', label: 'البنغالية' },
  { value: 'urdu', label: 'الأردية' },
  { value: 'turkish', label: 'التركية' },
  { value: 'korean', label: 'الكورية' },
  { value: 'vietnamese', label: 'الفيتنامية' },
  { value: 'thai', label: 'التايلاندية' },
  { value: 'persian', label: 'الفارسية' },
  { value: 'dutch', label: 'الهولندية' },
  { value: 'swedish', label: 'السويدية' },
  { value: 'greek', label: 'اليونانية' },
  { value: 'hebrew', label: 'العبرية' },
  { value: 'polish', label: 'البولندية' },
  { value: 'romanian', label: 'الرومانية' },
  { value: 'hungarian', label: 'الهنغارية' },
  { value: 'czech', label: 'التشيكية' },
  { value: 'slovak', label: 'السلوفاكية' },
  { value: 'danish', label: 'الدنماركية' },
  { value: 'finnish', label: 'الفنلندية' },
  { value: 'norwegian', label: 'النرويجية' },
  { value: 'icelandic', label: 'الأيسلندية' },
  { value: 'malay', label: 'الملايوية' },
  { value: 'indonesian', label: 'الإندونيسية' },
  { value: 'filipino', label: 'الفلبينية' },
  { value: 'burmese', label: 'البورمية' },
  { value: 'khmer', label: 'الخميرية' },
  { value: 'lao', label: 'اللاوية' },
  { value: 'mongolian', label: 'المنغولية' },
  { value: 'tamil', label: 'التاميلية' },
  { value: 'telugu', label: 'التيلجو' },
  { value: 'marathi', label: 'الماراثية' },
  { value: 'gujarati', label: 'الغوجاراتية' },
  { value: 'punjabi', label: 'البنجابية' },
  { value: 'malayalam', label: 'المالايالامية' },
  { value: 'sinhalese', label: 'السنهالية' },
  { value: 'nepali', label: 'النيبالية' },
  { value: 'pashto', label: 'البشتوية' },
  { value: 'kurdish', label: 'الكردية' },
  { value: 'swahili', label: 'السواحلية' },
  { value: 'hausa', label: 'الهوسا' },
  { value: 'yoruba', label: 'اليوروبا' },
  { value: 'igbo', label: 'الإيغبو' },
  { value: 'zulu', label: 'الزولو' },
  { value: 'xhosa', label: 'الخوسا' },
  { value: 'amharic', label: 'الأمهرية' },
  { value: 'somali', label: 'الصومالية' },
  { value: 'tigrinya', label: 'التيغرينية' },
  { value: 'afrikaans', label: 'الأفريكانية' },
  { value: 'georgian', label: 'الجورجية' },
  { value: 'armenian', label: 'الأرمنية' },
  { value: 'azerbaijani', label: 'الأذربيجانية' },
  { value: 'kazakh', label: 'الكازاخية' },
  { value: 'uzbek', label: 'الأوزبكية' },
  { value: 'turkmen', label: 'التركمانية' },
  { value: 'tajik', label: 'الطاجيكية' },
  { value: 'kirghiz', label: 'القيرغيزية' },
  { value: 'bashkir', label: 'الباشكيرية' },
  { value: 'chechen', label: 'الشيشانية' },
  { value: 'belarusian', label: 'البيلاروسية' },
  { value: 'ukrainian', label: 'الأوكرانية' },
  { value: 'serbian', label: 'الصربية' },
  { value: 'croatian', label: 'الكرواتية' },
  { value: 'bosnian', label: 'البوسنية' },
  { value: 'slovenian', label: 'السلوفينية' },
  { value: 'macedonian', label: 'المقدونية' },
  { value: 'latvian', label: 'اللاتفية' },
  { value: 'lithuanian', label: 'الليتوانية' },
  { value: 'estonian', label: 'الإستونية' },
  { value: 'galician', label: 'الجاليكية' },
  { value: 'catalan', label: 'الكاتالانية' },
  { value: 'basque', label: 'الباسكية' },
  { value: 'breton', label: 'البريتانية' },
  { value: 'welsh', label: 'الويلزية' },
  { value: 'irish', label: 'الإيرلندية' },
  { value: 'scottish_gaelic', label: 'الغيلية الاسكتلندية' },
  { value: 'maltese', label: 'المالطية' },
  { value: 'esperanto', label: 'الإسبرانتو' },
];

function CVMaker() {
  const initialCvData = {
    fullName: '',
    birthYear: '',
    birthMonth: '',
    birthDay: '',
    gender: '',
    maritalStatus: '',
    nationality: '',
    address: '',
    phoneNumber: '',
    emailAddress: '',
    professionalSummary: '',
    educationalQualifications: '',
    workExperience: '',
    skills: '',
    certifications: '',
    languages: [],
    hobbies: '',
    aboutMe: ''
  };

  const [cvData, setCvData] = useState(() => {
    const savedData = localStorage.getItem('cvData');
    return savedData ? JSON.parse(savedData) : initialCvData;
  });

  useEffect(() => {
    localStorage.setItem('cvData', JSON.stringify(cvData));
  }, [cvData]);

  const calculateCompletionPercentage = () => {
    const totalFields = Object.keys(cvData).length;
    const filledFields = Object.values(cvData).filter(value => {
      if (Array.isArray(value)) {
        return value.length > 0;
      }
      return value !== '';
    }).length;
    return Math.round((filledFields / totalFields) * 100);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setCvData(prevData => ({ ...prevData, [name]: value }));
  };

  const handleLanguagesChange = (selectedOptions) => {
    setCvData(prevData => ({ ...prevData, languages: selectedOptions }));
  };

  const handleSavePDF = () => {
    const input = document.getElementById('cv-preview');
    html2canvas(input, {
      scale: 4,
      useCORS: true,
      backgroundColor: '#ffffff',
    }).then((canvas) => {
      const imgData = canvas.toDataURL('image/png');
      const pdf = new jsPDF('p', 'mm', 'a4');
      const imgWidth = 210;
      const pageHeight = 297;
      const imgHeight = (canvas.height * imgWidth) / canvas.width;
      pdf.addImage(imgData, 'PNG', 0, 0, imgWidth, pageHeight);

      const fileName = `السيرة الذاتية الخاصة ب${cvData.fullName || 'المستخدم'}.pdf`;
      pdf.save(fileName);
    });
  };

  // Define the arrays for years, months, and days
  const years = Array.from({ length: 100 }, (_, i) => 1920 + i);
  const months = Array.from({ length: 12 }, (_, i) => i + 1);
  const days = Array.from({ length: 31 }, (_, i) => i + 1);

   return (
    <Container maxWidth="md" sx={{ py: 10, backgroundColor: '#202123', direction: 'rtl', textAlign: 'right' }}>
      <Typography variant="h4" sx={{ mb: 6, textAlign: 'center', fontWeight: 'bold', color: '#ffffff' }}>
        منشئ السيرة الذاتية
      </Typography>
      <Typography variant="h6" sx={{ mb: 4, textAlign: 'center', color: '#ffffff' }}>
        نسبة اكتمال السيرة الذاتية: {calculateCompletionPercentage()}%
      </Typography>
      <Typography variant="body1" sx={{ mb: 4, textAlign: 'center', color: '#ffffff' }}>
        نسبة قبولك بالمعلومات المتواجدة حاليا: {calculateCompletionPercentage() === 100 ? '100%' : 'غير مكتملة'}
      </Typography>
      <Box component="form" sx={{ display: 'flex', flexDirection: 'column', gap: 3, mb: 5 }}>
        <TextField 
          label="الاسم الكامل" 
          name="fullName" 
          value={cvData.fullName} 
          onChange={handleChange} 
          variant="outlined" 
          fullWidth 
          inputProps={{ dir: "rtl", style: { textAlign: 'right' } }} 
        />

        {/* Dropdowns for birth date */}
        <Box sx={{ display: 'flex', gap: 2 }}>
          <MuiSelect
            label="السنة"
            name="birthYear"
            value={cvData.birthYear}
            onChange={handleChange}
            displayEmpty
            variant="outlined"
            fullWidth
          >
            <MenuItem value="" disabled>السنة</MenuItem>
            {years.map(year => <MenuItem key={year} value={year}>{year}</MenuItem>)}
          </MuiSelect>
          <MuiSelect
            label="الشهر"
            name="birthMonth"
            value={cvData.birthMonth}
            onChange={handleChange}
            displayEmpty
            variant="outlined"
            fullWidth
          >
            <MenuItem value="" disabled>الشهر</MenuItem>
            {months.map(month => <MenuItem key={month} value={month}>{month}</MenuItem>)}
          </MuiSelect>
          <MuiSelect
            label="اليوم"
            name="birthDay"
            value={cvData.birthDay}
            onChange={handleChange}
            displayEmpty
            variant="outlined"
            fullWidth
          >
            <MenuItem value="" disabled>اليوم</MenuItem>
            {days.map(day => <MenuItem key={day} value={day}>{day}</MenuItem>)}
          </MuiSelect>
        </Box>
        <MuiSelect
          label="الجنس"
          name="gender"
          value={cvData.gender}
          onChange={handleChange}
          displayEmpty
          variant="outlined"
          fullWidth
        >
          <MenuItem value="" disabled>الجنس</MenuItem>
          <MenuItem value="ذكر">ذكر</MenuItem>
          <MenuItem value="أنثى">أنثى</MenuItem>
        </MuiSelect>

        <MuiSelect
          label="الحالة الاجتماعية"
          name="maritalStatus"
          value={cvData.maritalStatus}
          onChange={handleChange}
          displayEmpty
          variant="outlined"
          fullWidth
        >
          <MenuItem value="" disabled>الحالة الاجتماعية</MenuItem>
          <MenuItem value="متزوج">متزوج</MenuItem>
          <MenuItem value="مطلق">مطلق</MenuItem>
          <MenuItem value="اعزب">اعزب</MenuItem>
          <MenuItem value="ارمل">ارمل</MenuItem>
        </MuiSelect>
        <TextField 
          label="الجنسية" 
          name="nationality" 
          value={cvData.nationality} 
          onChange={handleChange} 
          variant="outlined" 
          fullWidth 
          inputProps={{ dir: "rtl", style: { textAlign: 'right' } }} 
        />
        <TextField 
          label="العنوان" 
          name="address" 
          value={cvData.address} 
          onChange={handleChange} 
          variant="outlined" 
          fullWidth 
          inputProps={{ dir: "rtl", style: { textAlign: 'right' } }} 
        />
        <TextField 
          label="رقم الهاتف" 
          name="phoneNumber" 
          value={cvData.phoneNumber} 
          onChange={handleChange} 
          variant="outlined" 
          fullWidth 
          inputProps={{ dir: "rtl", style: { textAlign: 'right' } }} 
        />
        <TextField 
          label="البريد الإلكتروني" 
          name="emailAddress" 
          value={cvData.emailAddress} 
          onChange={handleChange} 
          variant="outlined" 
          fullWidth 
          inputProps={{ dir: "rtl", style: { textAlign: 'right' } }} 
        />
        <TextField 
          label="نبذة عني" 
          name="aboutMe" 
          value={cvData.aboutMe} 
          onChange={handleChange} 
          multiline 
          rows={4} 
          variant="outlined" 
          fullWidth 
          inputProps={{ dir: "rtl", style: { textAlign: 'right' } }} 
        />
        <TextField 
          label="المؤهلات العلمية" 
          name="educationalQualifications" 
          value={cvData.educationalQualifications} 
          onChange={handleChange} 
          multiline 
          rows={4} 
          variant="outlined" 
          fullWidth 
          inputProps={{ dir: "rtl", style: { textAlign: 'right' } }} 
        />
        <TextField 
          label="الخبرات العملية" 
          name="workExperience" 
          value={cvData.workExperience} 
          onChange={handleChange} 
          multiline 
          rows={4} 
          variant="outlined" 
          fullWidth 
          inputProps={{ dir: "rtl", style: { textAlign: 'right' } }} 
        />
        <TextField 
          label="المهارات" 
          name="skills" 
          value={cvData.skills} 
          onChange={handleChange} 
          multiline 
          rows={4} 
          variant="outlined" 
          fullWidth 
          inputProps={{ dir: "rtl", style: { textAlign: 'right' } }} 
        />
        <TextField 
          label="الدورات التدريبية والشهادات" 
          name="certifications" 
          value={cvData.certifications} 
          onChange={handleChange} 
          multiline 
          rows={4} 
          variant="outlined" 
          fullWidth 
          inputProps={{ dir: "rtl", style: { textAlign: 'right' } }} 
        />
        <TextField 
          label="الاهتمامات الشخصية" 
          name="hobbies" 
          value={cvData.hobbies} 
          onChange={handleChange} 
          multiline 
          rows={4} 
          variant="outlined" 
          fullWidth 
          inputProps={{ dir: "rtl", style: { textAlign: 'right', color: '#ffffff' } }} 
        />
        <Select
          isMulti
          name="languages"
          options={languageOptions}
          value={cvData.languages}
          onChange={handleLanguagesChange}
          placeholder="اختر اللغات"
          styles={{
            control: (base) => ({
              ...base,
              textAlign: 'right',
              backgroundColor: '#202123',
              borderColor: '#ccc',
              boxShadow: 'none',
              '&:hover': {
                borderColor: '#b3b3b3',
              },
            }),
            menu: (base) => ({
              ...base,
              backgroundColor: '#202123',
            }),
            option: (base) => ({
              ...base,
              color: '#ffffff',
            }),
          }}
        />
        <Button variant="contained" onClick={handleSavePDF} sx={{ mt: 3, bgcolor: '#147a5c', '&:hover': { bgcolor: '#0d8c6d' }, fontWeight: 'bold' }}>
          حفظ كملف PDF
        </Button>
      </Box>

      <Box id="cv-preview" sx={{ 
        mt: 5, 
        p: 3, 
        border: '2px solid #ccc', 
        borderRadius: 2, 
        backgroundColor: '#ffffff', 
        boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)', 
        textAlign: 'right', 
        direction: 'rtl', 
        color: '#333333' 
      }}>
        <Typography variant="h5" sx={{ textAlign: "center", mb: 3, fontWeight: "bold", color: "#333333" }}>
        {cvData.fullName}
      </Typography>
      <Box sx={{ backgroundColor: "#f8f9fa", p: 4, borderRadius: 3 }}>
                
        {/* قسم معلومات التواصل */}
        <Typography variant="h5" sx={{ mb: 2, color: "#004080", fontWeight: "900" }}>
          <Email sx={{ verticalAlign: "middle", mr: 1 }} /> معلومات التواصل
        </Typography>
        <Box sx={{ borderBottom: "3px solid #007ACC", mb: 2 }}></Box>
        <Typography variant="body1" sx={{ mb: 2, color: "#007ACC", fontWeight: "bold" }}>
          <Email sx={{ verticalAlign: "middle", mr: 1 }} /> البريد الإلكتروني: {cvData.emailAddress}
        </Typography>
        <Typography variant="body1" sx={{ mb: 2, color: "#007ACC", fontWeight: "bold" }}>
          <Phone sx={{ verticalAlign: "middle", mr: 1 }} /> رقم الهاتف: {cvData.phoneNumber}
        </Typography>
        <Typography variant="body1" sx={{ mb: 2, color: "#333333", fontWeight: "bold" }}>
          <Cake sx={{ verticalAlign: "middle", mr: 1 }} /> تاريخ الميلاد: {cvData.birthYear}/{cvData.birthMonth}/{cvData.birthDay}
        </Typography>
        <Typography variant="body1" sx={{ mb: 2, color: "#333333", fontWeight: "bold" }}>
          <People sx={{ verticalAlign: "middle", mr: 1 }} /> الجنس: {cvData.gender} | الحالة الاجتماعية: {cvData.maritalStatus} | الجنسية: {cvData.nationality}
        </Typography>
        <Typography variant="body1" sx={{ mb: 2, color: "#333333", fontWeight: "bold" }}>
          <LocationOn sx={{ verticalAlign: "middle", mr: 1 }} /> العنوان: {cvData.address}
        </Typography>

        {/* قسم الخبرات والمهارات */}
        <Typography variant="h5" sx={{ mt: 4, mb: 2, color: "#004080", fontWeight: "900" }}>
          <Work sx={{ verticalAlign: "middle", mr: 1 }} /> الخبرات والمهارات
        </Typography>
        <Box sx={{ borderBottom: "3px solid #007ACC", mb: 2 }}></Box>
        <Typography variant="body1" sx={{ mb: 2, color: "#333333", fontWeight: "bold" }}>
          <Star sx={{ verticalAlign: "middle", mr: 1 }} /> المهارات: {cvData.skills}
        </Typography>
        <Typography variant="body1" sx={{ mb: 2, color: "#333333", fontWeight: "bold" }}>
          <Work sx={{ verticalAlign: "middle", mr: 1 }} /> الخبرات العملية: {cvData.workExperience}
        </Typography>

        {/* قسم التعليم والشهادات */}
        <Typography variant="h5" sx={{ mt: 4, mb: 2, color: "#004080", fontWeight: "900" }}>
          <School sx={{ verticalAlign: "middle", mr: 1 }} /> التعليم والشهادات
        </Typography>
        <Box sx={{ borderBottom: "3px solid #007ACC", mb: 2 }}></Box>
        <Typography variant="body1" sx={{ mb: 2, color: "#333333", fontWeight: "bold" }}>
          <School sx={{ verticalAlign: "middle", mr: 1 }} /> المؤهلات العلمية: {cvData.educationalQualifications}
        </Typography>
        <Typography variant="body1" sx={{ mb: 2, color: "#333333", fontWeight: "bold" }}>
          📜 الدورات التدريبية والشهادات: {cvData.certifications}
        </Typography>
        <Typography variant="body1" sx={{ mb: 2, color: "#333333", fontWeight: "bold" }}>
          <Language sx={{ verticalAlign: "middle", mr: 1 }} /> اللغات: {cvData.languages.map(lang => lang.label).join(', ')}
        </Typography>

        {/* قسم معلومات إضافية */}
        <Typography variant="h5" sx={{ mt: 4, mb: 2, color: "#004080", fontWeight: "900" }}>
          <Info sx={{ verticalAlign: "middle", mr: 1 }} /> معلومات إضافية
        </Typography>
        <Box sx={{ borderBottom: "3px solid #007ACC", mb: 2 }}></Box>
        <Typography variant="body1" sx={{ mb: 2, color: "#333333", fontWeight: "bold" }}>
          🎯 الاهتمامات الشخصية: {cvData.hobbies}
        </Typography>
        <Typography variant="body1" sx={{ mb: 2, color: "#333333", fontWeight: "bold" }}>
          <Info sx={{ verticalAlign: "middle", mr: 1 }} /> نبذة عني: {cvData.aboutMe}
        </Typography>
        </Box>
        </Box>      
    </Container>
  );
}

export default CVMaker;